.layout-container {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.layout-container .layout-main {
  margin-top: 46px;
  position: relative;
}
.layout-container .ant-layout-has-sider {
  height: 100%;
}
.layout-container .nodata-page {
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: fixed;
  top: 45px;
  height: 100%;
  z-index: 2;
}
.layout-container .nodata-page .nodata-wrap {
  width: 1rem;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -95%);
  -ms-transform: translate(-50%, -95%);
   -o-transform: translate(-50%, -95%);
      transform: translate(-50%, -95%);
  text-align: center;
  font-size: 14px;
  color: #A9A9A9;
  line-height: 14px;
}
.layout-container .nodata-page .nodata-wrap img {
  width: 0.6rem;
  height: 0.8rem;
  margin-bottom: 20px;
}



