.comp-alert-container {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 200;
}
.comp-alert-container .mask {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.3);
  position: absolute;
  top: 0;
  left: 0;
}
.comp-alert-container .alert-wrapper {
  width: 3.45rem;
  position: absolute;
  top: 50%;
  left: 50%;
  background: #fff;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
       -o-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  border-radius: 5px;
}
.comp-alert-container .alert-wrapper img.alert-success-icon {
  width: 0.4rem;
  margin: 0.35rem auto;
  display: block;
}
.comp-alert-container .alert-wrapper .alert-title {
  text-align: center;
  font-size: 16px;
  color: #333;
  font-weight: bold;
  margin-bottom: 0.1rem;
}
.comp-alert-container .alert-wrapper .alert-content {
  overflow: hidden;
  font-size: 12px;
  color: #9A9A9A;
  text-align: center;
}
.comp-alert-container .alert-wrapper .alert-button-group {
  padding: 0.2rem 0.5rem 0.5rem;
}



